// reducers/index.js
import { combineReducers } from 'redux' // combineReducers reducers
import { routerReducer } from 'react-router-redux' // routerReducer

import propertyReducer from './app/propertyReducer'

export default combineReducers({
  routing: routerReducer,
  property: propertyReducer
})



