import React from 'react'  // React
import { render } from 'react-dom' // render
import { Provider } from 'react-redux' // Provider store 
import { Router, browserHistory } from 'react-router' // Browser history  React Router  history
import { syncHistoryWithStore } from 'react-router-redux' // react-router-reduxsyncHistoryWithStorestore

import finalCreateStore from './src/store/configureStore'  //store
import DevTools from './src/containers/DevTools'  // ReduxDevTools
import reducer from './src/reducers'  // reducers
import routes from './src/routes'   // 

// storereducer
const store = finalCreateStore(reducer)

// historystore
const history = syncHistoryWithStore(browserHistory, store)

render(
  <Provider store={store}>
    <div>
      <Router history={history} routes={routes} />
      <DevTools />
    </div>
  </Provider>,
  document.getElementById('mount')
)



